import React from 'react'
import Slide from 'react-reveal/Slide'
import { Container, Row } from 'reactstrap'
import './InfoContainer.css'

function InfoContainer() {
    return (
        <div>
            <Container fluid className="InfoContainer">
                <Row className="Bio">
                    Hey I'm Alex, I have been creating websites for about 10 years now, starting out with video game fansites on GeoCities. I have professional experience designing and developing corporate pages for small businesses in the Dallas area, as well as some experience with backend technologies like Node, MongoDB, and Express from working on things like cool Discord bots! When I am not coding, I am watching the Dallas Mavericks or New Jersey Devils. 
                </Row>
                <Row className="Tech">
                    Some of the technologies I am proficient with include: 
                </Row>
                <Slide left>
                <Row className="TechList">
                    <div className="TechLogo">
                        <img src="img/html.png" alt="" className="TechImage"/>
                        <p>HTML</p>  
                        <p>★★★★★</p>       
                    </div>
                    <div className="TechLogo">
                        <img src="img/dcc.png" alt="" className="TechImage"/>
                        <p>CSS</p> 
                        <p>★★★★</p>       
                    </div>
                    <div className="TechLogo">
                        <img src="img/js.png" alt="" className="TechImage"/>
                        <p>JavaScript</p> 
                        <p>★★★★</p>       
                    </div>
                    <div className="TechLogo">
                        <img src="img/bb.png" alt="" className="TechImage"/>
                        <p>Bootstrap</p> 
                        <p>★★★★★</p>       
                    </div>
                    <div className="TechLogo">
                        <img src="img/react.png" alt="" className="TechImage"/>
                        <p>React.js</p> 
                        <p>★★★★</p>       
                    </div>
                    <div className="TechLogo">
                        <img src="img/node.png" alt="" className="TechImage"/>
                        <p>Node.js</p> 
                        <p>★★★</p>       
                    </div>
                    <div className="TechLogo">
                        <img src="img/mongo.png" alt="" className="TechImage"/>
                        <p>MongoDB</p> 
                        <p>★★★</p>       
                    </div>
                    <div className="TechLogo">
                        <img src="img/express.png" alt="" className="TechImage"/>
                        <p>Express</p> 
                        <p>★★★</p>       
                    </div>
                </Row>
                </Slide>
            </Container>
        </div>
        
    )
}

export default InfoContainer